import "./App.css";
import React, { useEffect, useRef, useState } from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Card } from 'react-bootstrap';
import gsap from "gsap";
import codeImage from "./image/code_is_animation.png";


function App() {
  const textRef = useRef(null);
  const linksRef = useRef([]);
  const imageRef = useRef(null);

  const [rotationSpeed, setRotationSpeed] = useState(0);

  const experienceData = [
    {
      title: 'Software Engineer',
      company: 'ABC Tech Solutions',
      location: 'New York, NY',
      date: 'June 2019 - Present',
      description:
        'Developed web applications using React.js and Node.js. Collaborated with cross-functional teams to deliver high-quality software solutions.'
    },
    {
      title: 'Frontend Developer Intern',
      company: 'XYZ Startup',
      location: 'San Francisco, CA',
      date: 'January 2019 - May 2019',
      description:
        'Worked on UI/UX design and implemented responsive user interfaces using HTML, CSS, and JavaScript.'
    }
  ];


  useEffect(() => {
    const textElement = textRef.current;
    const linkElements = textElement.querySelectorAll("a");

    // Text and link animations
    gsap.fromTo(textElement, { y: 20, opacity: 0 }, { y: 0, opacity: 1, duration: 1, ease: "power3.out" });
    gsap.fromTo(linkElements, { y: -10, opacity: 0 }, { y: 0, opacity: 1, duration: 0.5, ease: "power3.out", stagger: 0.2 });

    // Mousemove event listener for image rotation
    const imageElement = imageRef.current;
    imageElement.addEventListener("mousemove", handleMouseMove);
    imageElement.addEventListener("mouseleave", handleMouseLeave);

    return () => {
      imageElement.removeEventListener("mousemove", handleMouseMove);
      imageElement.removeEventListener("mouseleave", handleMouseLeave);
    };
  }, []);

  const handleMouseMove = (e) => {
    const imageElement = imageRef.current;
    const rect = imageElement.getBoundingClientRect();

    // Calculate rotation speed based on mouse position
    const movementX = (e.clientX - rect.left - rect.width / 2) / (rect.width / 2);
    const movementY = (e.clientY - rect.top - rect.height / 2) / (rect.height / 2);

    // Set rotation speed
    setRotationSpeed(movementX * 0.1); // Adjust speed multiplier as needed
  };

  const handleMouseLeave = () => {
    // Reset rotation speed when mouse leaves the image
    setRotationSpeed(0);
  };

  const addLinkRef = (el) => {
    if (el && !linksRef.current.includes(el)) {
      linksRef.current.push(el);
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <Navbar collapseOnSelect expand="lg" className="custom-navbar">
          <Container>
            <Navbar.Brand href="#home">Kundan Kumar</Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="me-auto"></Nav>
              <Nav>
                <Nav.Link href="#experience">Experience</Nav.Link>
                <Nav.Link href="#project">Project</Nav.Link>
                <Nav.Link href="#contact">Contact</Nav.Link>
                <Nav.Link href="#blog">Blog</Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        <Container className="intro-section">
          <Row className="p-2">
            <h1>Kundan Kumar</h1>
            <h4>Software Developer-2 Group.one (One.com)</h4>
          </Row>
          <Row className="p-2">
              <Col>
                <p ref={textRef} className="text-content">
                  Currently, I am working as a Software Developer || at One.com India Pvt Ltd. Prior to joining{" "}
                  <a href="https://one.com" ref={addLinkRef}>
                    One.com
                  </a>
                  , I worked as a Software Developer at{" "}
                  <a href="https://finvolv.ai/" ref={addLinkRef}>
                    finvolv.ai (Kuliza Technology)
                  </a>
                  .
                  I am pursuing my master's in Artificial Intelligence and Machine Learning from the Indian Institute of
                  Technology Patna (IITP). I completed my B.Tech. from the Birla Institute of Technology Pilani in
                  Computer Science and Engineering. After completing my B.Tech., I worked at Teligenz Tech Solution
                  Software Developer, Bangalore.
                </p>
              </Col>
              <Col>
                <img
                  src={codeImage}
                  ref={imageRef}
                  className="rotate-image"
                  style={{ transform: `rotate(${rotationSpeed * 360}deg)`, transition: "transform 0.1s ease-out" }}
                />
              </Col>
          </Row>
        </Container>
      </header>
      <main>
      <div className="experience-section">
      <Container>
        <h2 className="section-title">Experience</h2>
        {experienceData.map((experience, index) => (
          <Card key={index} className="experience-card">
            <Card.Body>
              <Card.Title>{experience.title}</Card.Title>
              <Card.Subtitle className="mb-2 text-muted">{experience.company}</Card.Subtitle>
              <Card.Text className="text-muted">{experience.location}</Card.Text>
              <Card.Text className="text-muted">{experience.date}</Card.Text>
              <Card.Text>{experience.description}</Card.Text>
            </Card.Body>
          </Card>
        ))}
      </Container>
    </div>
      </main>
    </div>
  );
}

export default App;
